.SideDrawer{

position: fixed;
width: 280px;
max-width: 70%;
height: 100%;
left: 0;
top:0;
z-index: 200;
background-color: #fff;
padding: 23px 16px;
box-sizing: border-box;
transition: transform 0.3s ease-out;

}

@media (min-width:500px){
.SideDrawer{
    display: none;
}
}


.Open{
transform: rotateX(0);
}

.Close{
transform: translateX(-100%);
}

.Logo{
   height: 11%; 
   margin-bottom: 32px;
}